body {
  background-color: skyblue;
  text-align: center;
  display: flex;
  justify-content: center;
  margin: auto;
}

.cardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card {
  width: 300px; /* 각 카드의 원하는 너비 설정 */
  margin: 10px;
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: scale(1.05);
}

.detail-card {
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  margin: auto;
  max-width: 800px;
  padding: 20px;
}
